.cards-block {
  align-items: stretch;
  flex-wrap: wrap;
  margin-top: 40px;
}

.cards-block .card {
  position: relative;
  display: block;
  width: 100%;
  padding: 24px 16px;
  margin-bottom: 2px;
  background: #fde033;
  border: 3px solid #fde033;
  color: #242424;
  border-radius: 40px;
  text-decoration: none;
  overflow: visible;
}
.cards-block.old .card.card-XL {
  overflow: hidden;
}
.cards-block .card:after {
  content: "";
  position: absolute;
  z-index: 9;
  height: 4px;
  width: 60%;
  bottom: -6px;
  left: 50%;
  transform: translateX(-50%);
  background: #fde033;
}
.cards-block .card:last-child:after {
  display: none;
}
.cards-block.old .card:after {
  background: #583fd2;
}
.cards-block.old.wave .card:after,
.cards-text-block .card:after {
  background: #5ea9c1;
}

.cards-block .card.mobile {
  position: relative;
  left: unset;
  visibility: visible;
}
.cards-block .card.desktop {
  position: absolute;
  display: block;
  left: -100vw;
  visibility: hidden;
}

.cards-block .card .main-image {
  width: 100%;
  object-fit: contain;
  object-position: left top;
  background-color: #fde033;
}

.cards-block .card > div {
  justify-content: space-between;
}

.cards-block .card .title {
  margin: 28px 0 8px 0;
  font-family: "Druk Wide";
  font-size: 18px;
}

.cards-block .card .tags,
.cards-block .card .hover-btn {
  display: none;
}

.cards-block .card .tags {
  flex-wrap: wrap;
  margin-top: 10px;
}
.cards-block .card .tags span {
  display: inline-block;
  padding: 5px 18px 6px 18px;
  margin: 0 2px 2px 0;
  background: #fde033;
  border-radius: 20px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
}

.cards-block .card .hover-btn {
  width: 120px;
  height: 120px;
  margin-top: 28px;
  justify-content: center;
  background: #242424;
  border-radius: 50%;
}
.cards-block .card.card-M .hover-btn {
  background: #fde033;
}
.cards-block .card .hover-btn img {
  width: 70%;
  object-fit: contain;
}

.cards-block.old .card {
  background: #583fd2;
  border-color: #583fd2;
  color: #ffffff;
}
.cards-block.old .card .main-image {
  background-color: transparent;
}
.cards-block.old .card .hover-btn,
.cards-text-block .card .hover-btn {
  background: #fde033;
}

.cards-text-block {
  display: flex;
  flex-direction: column;
}

.cards-block.old.wave .card,
.cards-text-block .card {
  background: #5ea9c1;
  border-color: #5ea9c1;
  color: #ffffff;
}

.cards-block .card-XL {
  margin: 0 -20px 2px -20px;
  width: calc(100% + 40px);
}

.cards-block .card-XL .title {
  margin-top: 0;
}
.cards-block .card-XL img {
  margin-top: 20px;
}

.cards-block .card-XL .main-image {
  margin-bottom: -35px;
}

.cards-block.cards-block-4 {
  margin-top: 0;
}

@media screen and (min-width: 1024px) {
  .cards-block {
    margin-top: 20px;
  }

  .cards-block {
    padding: 42px 24px;
  }

  .cards-block-3 {
    padding-bottom: 0;
  }
  .cards-block-4 {
    padding-top: 0;
  }

  .cards-block .card:after {
    display: none;
  }
  .cards-block .card.card-divider-right:after {
    display: block;
    width: 4px;
    height: 60%;
    bottom: 20%;
    right: -6px;
    left: unset;
    transform: none;
  }
  .cards-block .card.card-M:last-of-type:after {
    display: none;
  }

  .cards-block .card:hover,
  .cards-block.wave .card:hover,
  .cards-text-block .card:hover {
    border-color: #242424;
    background: #ffffff;
  }

  .cards-block.old .card:hover,
  .cards-text-block .card:hover {
    color: #242424;
  }

  .cards-block .card .content {
    max-width: 75%;
  }

  .cards-block .card .title {
    font-size: 32px;
    line-height: 34px;
  }
  .cards-block .card-S .title {
    font-size: 20px;
    line-height: 24px;
  }

  .cards-block .card .tags,
  .cards-block .card .hover-btn {
    display: flex;
    visibility: hidden;
  }

  .cards-block-2 .card,
  .cards-block-2 .card .content {
    align-items: flex-start;
  }

  .cards-block .card:hover .tags,
  .cards-block .card:hover .hover-btn {
    visibility: visible;
  }

  .cards-block .card-M {
    width: calc(50% - 1px);
    margin: 0;
  }
  .cards-block .card-M:nth-child(2n-1) {
    margin-right: 1px;
  }
  .cards-block .card-M:nth-child(2n) {
    margin-left: 1px;
  }
  .cards-block .card-L,
  .cards-block .card-XL {
    width: 100%;
    margin-top: 12px;
  }

  .cards-block-6 .card:nth-child(1),
  .cards-block-6 .card:nth-child(2) {
    margin-bottom: 1px;
  }
  .cards-block-6 .card:nth-child(3),
  .cards-block-6 .card:nth-child(4) {
    margin-top: 1px;
  }

  .cards-block .card-XL {
    position: relative;
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }

  .cards-block .card-XL .flex {
    align-items: flex-start;
  }

  .cards-block .card-XL .main-image {
    height: 640px;
    width: unset;
    margin-bottom: -32px;
    margin-top: 0;
  }

  .cards-block.cards-block-4 {
    margin-top: 12px;
  }
  .cards-block-4 .card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .cards-block .card-S {
    width: calc(33.3% - 2px);
    margin: 0 1px;
  }
  .cards-block .card-S:first-child {
    margin-left: 0;
  }
  .cards-block .card-S:last-child {
    margin-right: 0;
  }

  .cards-text-block {
    align-items: center;
    justify-content: flex-end;
    flex-direction: row;
  }

  .cards-text-block .simple-text {
    margin-right: 60px;
  }
  .cards-text-block .cards-block {
    margin-left: 60px;
  }
  .cards-text-block .card {
    max-width: 680px;
  }
  .cards-text-block .card:nth-child(2) {
    margin-left: -60px;
  }
}

@media screen and (min-width: 1280px) {
  .cards-block .card.mobile {
    position: absolute;
    display: block;
    left: -100vw;
    visibility: hidden;
  }

  .cards-block .card.desktop {
    position: relative;
    left: unset;
    visibility: visible;
  }

  .cards-block .card-S .title {
    font-size: 32px;
    line-height: 34px;
  }

  .cards-block .card-XL .hover-btn {
    position: absolute;
    bottom: 20px;
    left: 20px;
  }
}
